@use 'global' as *;

.blog-card {
  width: 96.52%;
  background-color: $white;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
  margin-inline:auto;
  @include mq('md') {
    width: rem(333);
  }

}

.blog-card__inner {
  padding: rem(16) rem(16) rem(24);
}

.blog-card__image {
  aspect-ratio: 30 / 20;
}

.blog-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.blog-card__content {
  margin-top: rem(20);
  @include mq('md') {
    margin-top: rem(16);
  }
}

.blog-card__date {
  font-family: $english-font;
  font-size: rem(12);
  //line-height: calc(18 / 12);
  line-height: 1;
  color: $green;
}

.blog-card__title {
  font-family: $sans;
  font-size: rem(16);
  line-height: calc(24 / 16);
  font-weight: 500;
  padding-bottom: rem(4);
  border-bottom: solid 1px $green-accent;
}

.blog-card__text {
  margin-top: rem(16);
  line-height: calc(24 / 16);
}
