@use 'global' as *;

.footer {
  //TODO: 確認後削除
  margin-top: rem(100);
  background-color: $green-accent;
  color:$white;
}

.footer__inner {
  padding: rem(48) rem(15) rem(58);
  @include mq('md') {
    padding: rem(80) 12.5%;
  }
}

.footer__images-container{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  @include mq('md') {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:flex-end;
  }
}

.footer__logo {
  width: rem(120);
  aspect-ratio: 120 / 45;
  @include mq('md') {
    width: rem(200);
    aspect-ratio: 200 / 75;

  }
}

.footer__logo img{
  width: 100%;
  height: auto;

}

.footer__sns-icons {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: rem(11);
  @include mq('md') {
    margin-top: 0;
    //TODO: pp 確認
    margin-bottom: rem(10);
    margin-left: rem(40);
  }
}

.footer__sns-icons img{
  width: 100%;
  height: auto;
}

.footer__navigation {
  margin-top: rem(40);
  @include mq('md') {
    margin-top: rem(16);
  }
}

.footer__copy{
  margin-top: rem(40);
  font-family: $english-font;
  font-weight: $regular;
  font-size: rem(12);
  line-height: calc(18 / 12);
  letter-spacing: 0.02em;
  @include mq('md') {
    margin-top: rem(80);
    text-align: center;
    font-size: rem(14);
    line-height: calc(18 / 14);
  }
}